  background-color: #0B132B;
}

.detail-container {
  width: 500px;
  height: 800px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  background-color: #0B132B;
}

.navbar {
  width: 100%;
  height: 15px;
  display: flex;
  justify-content: flex-start;
  padding: 10px;
  background-color: #0B132B;
}

.back-button {
  background: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) no-repeat center center;
  background-size: contain;
  border: none;
  width: 100%; /* 버튼 크기 조정 */
  height: 30px; /* 버튼 크기 조정 */
  cursor: pointer;
  border-radius: 90px;
}


.image-container {
  margin-top: 35px;
  width: 100%; /* 컨테이너 너비 */
  height: 350px;
  display: flex;
  justify-content: center; /* 가운데 정렬 */
}

.product-image {
  /* max-width: 300px;  */
  max-height: 150px; /* 이미지 최대 높이 지정 */
  object-fit: contain;
}

